.footer-container {
	background-color: #242424;
	padding: 4rem 0 2rem 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.footer-subscription {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin-bottom: 24px;
	padding: 24px;
	color: #fff;
	& > p {
		font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
			'Lucida Sans', Arial, sans-serif;
	}
	&-heading {
		margin-bottom: 24px;
		font-size: 24px;
	}

	&-text {
		margin-bottom: 24px;
		font-size: 20px;
	}
}

.footer-input {
	padding: 8px 20px;
	border-radius: 2px;
	margin-right: 10px;
	outline: none;
	border: none;
	font-size: 10px;
	margin-bottom: 16px;
	border: 1px solid #fff;
}

.footer-links {
	width: 100%;
	max-width: 1000px;
	display: flex;
	justify-content: center;
}

.footer-link {
	&-wrapper {
		display: flex;
	}
	&-items {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		margin: 16px;
		text-align: left;
		width: 160px;
		box-sizing: border-box;
		& h2 {
			margin-bottom: 16px;
		}

		& > h2 {
			color: #fff;
		}

		& a {
			color: #fff;
			text-decoration: none;
			margin-bottom: 8px;
			&:hover {
				color: #e9e9e9;
				transition: 0.3s ease-out;
			}
		}
	}
}

.footer-email-form {
	margin-bottom: 32px;
}

.footer-input::placeholder {
	color: #b1b1b1;
}

.social-icon-link {
	color: #fff;
	font-size: 24px;
}

.social-media {
	max-width: 1000px;
	width: 100%;
	&-wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 90%;
		max-width: 1000px;
		margin: 40px auto 0 auto;
	}
}

.social-icons {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 240px;
}

.social-logo {
	color: #fff;
	margin-left: 20px;
	cursor: pointer;
	text-decoration: none;
	font-size: 2rem;
	display: flex;
	align-items: center;
	margin-bottom: 16px;
}

.website-rights {
	color: #fff;
	margin-bottom: 16px;
}

@media screen and (max-width: 820px) {
	.footer {
		&-links {
			padding-top: 2rem;
		}
		&-input {
			width: 100%;
		}
		&-link-wrapper {
			flex-direction: column;
		}
	}
	.btn {
		width: 100%;
	}
	.social-media-wrapper {
		flex-direction: column;
	}
}
